<template>
  <div class="blackboardWriting">
    <head-top title="板书"></head-top>
    <van-pull-refresh v-model="isLoading" @refresh="getDataList" class="pull_fresh">
      <r-image-text ref="imageText" v-if="dataList.length > 0" :data-list="dataList">
      </r-image-text>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>
  </div>
</template>

<script>
import RImageText from "@/components/r-image/r-image-text";

export default {
  name: "",
  components: { RImageText },
  props: {},
  data() {
    return {
      dataList: [],
      isLoading: false,
      // imagePreview: null
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.isLoading = true;
      this.dataList = [];

      let reqData = { ciID: this.$store.getters.courseID };
      this.$mainHttp.boardNote
        .getList(reqData)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push({
                img: this.$mainHttp.resourse.picture(item.BNContent, 4),
                state: item.State,
                title: item.BNTitle,
                time: item.CreateTime,
                id: item.BNID,
                content: item.BNContent
              });
            });
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    }
  }
};
</script>


